.contact-footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 50px;
  }
  
  .contact-info h3 {
    color: var(--third-color);
    
  }
  
  .wrapper {
    display: flex;
    padding: 50px;
    grid-gap: 50px;
    gap: 50px;
    justify-content: center;
    align-items: center;
  }
  
  form {
  width: 50%;
  background: var(--fourth-color);
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0 5px 10px 0 var(--third-color);
  }
  
  .details {
    display: flex;
    flex-direction: column;
    grid-gap: 15px;
    gap: 15px;
  }
  
  form input, textarea {
    padding: 10px 20px;
    outline: none;
    border: 1px solid var(--third-color);
  
  }
  
  textarea {
    width: 100%;
    resize: none;
  }
  
  .message {
    margin: 15px 0;
  }
  
  .message p {
    margin-bottom: 10px;
    color: var(--secondary-color)
  }
  
  form button {
    padding: 5px 15px;
    font-size: 1rem;
    border: 3px solid var(--third-color);
    color: var(--secondary-color);
    box-shadow: 4px 2px 0 0 var(--secondary-color);
    cursor: pointer;
  }
  
  .contact-info {
    width: 50;
    display: flex;
    flex-direction: column;
    grid-gap: 30px;
    gap: 30px;
  }
  
  .contact-body {
    color: lightgrey;
  }
  .socials {
    color: var(--secondary-color)
  }
  
  .social a {
    display: inline-block;
  }

  @media (max-width: 800px) {
    .contact-section {
        padding: 15px;
      }
      .wrapper {
        flex-direction: column;
        padding: 15px;
      }
    
      form {
        width: 100%;
      }
    
      .contact-info {
        width: 100%;
      }
    
      .contact-info h3 {
          font-weight: 700;
      }
  }